import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const routes = [
	{
		path: '/',
		component: () => import('@/views/Home.vue')
		
	},
	// Redirect to starterkit page, if no match found
    {
        path: '*',
        redirect: '/'
    }
]

