import Cookies from 'js-cookie'

const savedLang = Cookies.get('languageStatus')
const defaultLang = savedLang ? savedLang : "zh"

const lang = {
  state: {
    language: defaultLang
  },
  mutations: {
    TOGGLE_LANGUAGE: (state, lang) => {
      Cookies.set('languageStatus', lang)
      state.language = lang
    },
  },
  actions: {
    ToogleLanguage({ commit }, lang) {
      console.log(lang)
      commit('TOGGLE_LANGUAGE', lang)
    }
  }
}

export default lang
