import Vue from 'vue'
import { Web3Provider } from '@ethersproject/providers'
import { formatUnits } from '@ethersproject/units'
import Connector from '@/helpers/connectors'
//import { getInstance } from '@snapshot-labs/lock/plugins/vue3'
import getProvider from '@/helpers/provider'
import store from '@/store'
import config from '@/helpers/config'
import networks from '@/helpers/networks'

let wsProvider
let auth
const defaultNetwork =
	process.env.VUE_APP_DEFAULT_NETWORK || Object.keys(networks)[0]

if (wsProvider) {
  wsProvider.on('block', blockNumber => {
    store.commit('GET_BLOCK_SUCCESS', blockNumber);
  });
}

const state = {
	account: null,
	name: null,
	chainId: null,
	balances: null,
	blockNumber: 0,
	network: networks[defaultNetwork]
}

const mutations = {
	LOGOUT(_state) {
		Vue.set(_state, 'account', null)
		Vue.set(_state, 'name', null)
		Vue.set(_state, 'balances', {})
		console.log('LOGOUT')
	},
	HANDLE_CHAIN_CHANGED(_state, chainId) {
		if (!networks[chainId]) {
			networks[chainId] = {
				...networks[defaultNetwork],
				chainId,
				name: 'Unknown',
				network: 'unknown',
				unknown: true
			}
		}
		_state.network = networks[chainId]
	},
	WEB3_SET(_state, payload) {
		Object.keys(payload).forEach(key => {
		_state[key] = payload[key];
	});
  }
}

const actions = {
	login: async ({ commit, dispatch }, connectorType = 'ETH') => {
		const connector = new Connector(connectorType)
		if (connector.isLoggedIn()) {
			auth = await connector.connect()
			auth.web3 = new Web3Provider(auth) 

			await dispatch('loadProvider')
		}
	},
	loadProvider: async ({ commit, dispatch }) => {
		try {
			if (auth.removeAllListeners) {
				auth.removeAllListeners()
			}

			if (auth.on) {
				auth.on('chainChanged', async chainId => {
					commit('HANDLE_CHAIN_CHANGED', parseInt(formatUnits(chainId, 0)))
				})

				auth.on('accountsChanged', async accounts => {
					if (accounts.length !== 0) {
						commit('WEB3_SET', { account: accounts[0] })
						await dispatch('loadProvider')
					}
				})

				let network, accounts
				try {
					[network, accounts] = await Promise.all([
						auth.web3.getNetwork(),
						auth.web3.listAccounts()
						])
				} catch(e) {
					console.log(e)
				}

				commit( 'HANDLE_CHAIN_CHANGED', network.chainId )
				const account = accounts.length > 0 ? accounts[0] : null

				const balance = await auth.web3.getBalance(account)
				commit('WEB3_SET', { account: account, balances: balance })
			}
		} catch (e) {
			commit('WEB3_SET', { account: null, name: null, balances: null})
			return Promise.reject(e)
		}
	}
}

export default {
	state,
	mutations,
	actions
}